import Header from './modules/header/components/Header';
import './fonts/stylesheet.css';
import './sass/style.scss';
import Banner from './modules/banner/components/Banner';
import Reputation from './modules/reputation/components/Reputation';
import Services from './modules/services/components/Services';
import Industry from './modules/industry/components/Industry';
import Case from './modules/case/components/Case';
import Testimonials from './modules/testimonials/components/Testimonials';
import News from './modules/news/components/News';
import Footer from './modules/footer/components/Footer';
import { useState } from 'react';

function App() {

    const [mobileMenuShow, setMobileMenuShow] = useState(false);

    if (mobileMenuShow) {
        document.body.style.overflow = 'hidden';
    } else {
        document.body.style.overflow = 'auto';
    }

    function handleMobileMenuShow() {
        setMobileMenuShow((mobileShowState) => {
            return !mobileShowState;
        });
    }

    return (
        <>
            <Header mobileMenuShow={mobileMenuShow} handleMobileMenuShow={handleMobileMenuShow} />
            <div className="main">
                <Banner />
                <Reputation />
                <Services />
                <Industry />
                <Case />
                <Testimonials />
                <News />
                <Footer />
                <div className={"background" + (mobileMenuShow ? " active" : "")} onClick={handleMobileMenuShow}/>
            </div>
        </>
    );
}

export default App;
