import React, { useEffect, useRef, useState } from 'react';
import Button from '../../../common/Button';

import thinkboungLogo from '../../../icons/thinkbound.png';
import logo from '../../../images/logo/logo.png';

import gsap from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import Burger from './../../../common/icons/Burger';

gsap.registerPlugin(ScrollTrigger);

let navLinks = [
    { title: 'Home', id: 1 },
    { title: 'About', id: 2 },
    { title: 'Services', id: 3 },
    { title: 'Industry Success', id: 4 },
    { title: 'Showcase', id: 5 },
    { title: 'Contact', id: 6 }
]

export default function Header({mobileMenuShow, handleMobileMenuShow}) {

    const itemsRef = useRef([]);

    const menuRef = useRef(null);

    let headerRef = useRef(null);

    const [mobileButtonShow, setMobileButtonShow] = useState(false);

    window.addEventListener('resize', () => {
        handleMobileButtonShow(setMobileButtonShow);
    });

    useEffect(() => {
        handleMobileButtonShow(setMobileButtonShow);

        let links = itemsRef.current;

        let header = headerRef.current;

        ScrollTrigger.create({
            start: header.clientHeight / 2,
            end: 'bottom',
            toggleClass: {
                targets: '.header',
                className: 'has-scrolled'
            },
            onEnter: ({ direction }) => navAnimation(links, direction),
            onLeaveBack: ({ direction }) => navAnimation(links, direction)
        })
    }, [])
    
    function addLinkClass(e) {
        e.target.classList.add('animate-out');
    }

    function removeLinkClass(e) {
        e.target.classList.remove('animate-out');
    }

    let menu = menuRef.current;

    if (menu) {
        if (mobileMenuShow) { 
            let links = menu.children;
            let height = Array.from(links).reduce((acc, item) => {
                return acc += (item.clientHeight + 1);
            }, 0)
            menu.style.height = height + 'px';
        } else {
            menu.style.height = 0;
        }
    }

    return (
        <header className="header" ref={headerRef}>
            <div className="header__wrapper container container--header">
                <a href="/" className="logo">
                    <img src={logo} alt="" className="logo__image" />
                </a>
                <div className={"header__menu" + (mobileMenuShow ? " opened" : "")}>
                    <ul className="header__menu-list" ref={menuRef}>
                        {navLinks.map((item, i) => {
                            return (
                                <li onMouseLeave={addLinkClass} onTransitionEnd={removeLinkClass} ref={el => itemsRef.current.push(el)} key={item.id} className="header__menu-item">
                                    <a href="/" className={"header__menu-link" + (i === 0 ? " active" : "")}>{item.title}</a>
                                </li>
                            );
                        })}
                    </ul>
                </div>
                <Button type='link' icon={thinkboungLogo} href="" className="header__button" text="Start project" />
                {mobileButtonShow ?
                    <button className={"header__mobile-button" + (mobileMenuShow ? " opened" : "")} onClick={handleMobileMenuShow}>
                        <Burger className="header__mobile-icon" />
                    </button> : null}
            </div>
        </header>
    )
}

function navAnimation(links, direction) {
    const down = direction === 1;
    return gsap.to(links, {
        duration: 0.3,
        stagger: 0.05,
        autoAlpha: () => down ? 0 : 1,
        y: () => down ? 20 : 0,
        ease: 'Power4.out'
    })
}

function handleMobileButtonShow(setMobileButtonShow) {
    if (window.matchMedia("(max-width: 989px)").matches) {
        setMobileButtonShow(true);
    } else {
        setMobileButtonShow(false);
    }
}
